document.querySelectorAll('.sidebar li a').forEach((element) => {
    element.classList.remove('bg-active', 'text-active-text');
});

document.querySelectorAll('.sidebar li:nth-child(2) a').forEach((element) => {
    element.classList.add('bg-active', 'text-active-text');
});

function country2flag(countryCode) {
    const codePoints = countryCode.toUpperCase().split('').map(char => 127397 + char.charCodeAt());
    return String.fromCodePoint(...codePoints);
}

document.querySelectorAll('.country').forEach((element) => {
    element.innerText = country2flag(element.innerText)
});

document.querySelectorAll('.joined-date').forEach((element) => {
    if(element.innerText !== ""){
        element.innerText = moment(element.innerText, "YYYY-MM-DD").fromNow();
    }
});

